import Loading from 'react-loading-bar';
import 'react-loading-bar/dist/index.css';
import { useSelector, useThemeConfig } from '@topwrite/common';

interface Props {
    loading?: boolean;
}

export default function LoadingBar({ loading: defaultLoading = false }: Props) {

    const { loading } = useSelector('page');
    const { primaryColor } = useThemeConfig();

    return <Loading show={loading || defaultLoading} color={primaryColor} />;
}
